.dice-container {
  --size: 50px;
  --move: calc(var(--size) / 2);
  --dot-size: calc(var(--size ) / 7 );
  display: grid;
  grid-template-columns: repeat(5,1fr);
  gap: 20px;
  margin-bottom: 40px;
  perspective: 300px;
  @media (max-width:411px) {
    --size: 40px;
    gap: 10px; }
  .die {
    height: var(--size);
    width: var(--size);
    cursor: pointer;
    position: relative;
    transform-style: preserve-3d;
    transition: 400ms;
    --four: -13px -13px , 13px 13px, -13px 13px, 13px -13px;
    $anglesY: (two:180,three: 270,four:90);
    $anglesX: (five:90, six: -90);
    @each $c, $a in $anglesY {
      &.#{$c} {
        transform: rotateY($a+deg); } }
    @each $c, $a in $anglesX {
      &.#{$c} {
        transform: rotateX($a+deg); } }
    .die-face {
      width: 100%;
      height: 100%;
      background-color: var(--main, black);
      border-radius: 4px;
      position: absolute;
      border: 1px solid var(--second);
      transition-duration:  250ms;
      color: var(--second,#fff);
      &::before {
        content: '';
        width: var(--dot-size);
        height: var(--dot-size);
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        border-radius: 50%; } }
    .one,
    .three,
    .five {
      &::before {
        background: currentColor; } }
    .four,.five {
      &::before {
        box-shadow: var(--four); } }
    .one {
      transform: translateZ(var(--move)); }
    .two {
      transform: translateZ(calc(var(--move) * -1));
      &::before {
        box-shadow: -13px 13px , 13px -13px; } }
    .three {
      transform: rotateY(90deg) translateZ(var(--move));
      &::before {
        box-shadow: -13px -13px , 13px 13px; } }
    .four {
      transform: rotateY(-90deg) translateZ(var(--move)); }
    .five {
      transform: rotateX(-90deg) translateZ(var(--move)); }
    .six {
      transform: rotateX(90deg) translateZ(var(--move));
      &::before {
        box-shadow: var(--four) , 13px 0 , -13px 0; } }
    &.active .die-face {
      background-color: #00A590;
      color: #FFF7E9; } } }
@keyframes rotate {
  100% {
    transform: rotateX(360deg) rotateY(360deg); } }
