* {
  box-sizing: border-box;
}
:root {
  --main: #fbb666;
  --second: #0b2434;
}
body {
  margin: 0;
  background-color: #0b2434;
  padding: 20px;
  font-family: "Karla", sans-serif;
}

main {
  color: #C3FCF1;
  background-image: linear-gradient(
    190deg,
    #432371,
    #714674,
    #9f6976,
    #cc8b79,
    #faae7b
  );
  margin-inline:auto;
  max-width: 800px;
  border-radius: 5px;
  padding: 20px;
  padding-top: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: relative;
}

.title {
  font-size: 40px;
  margin: 0;
}

.instructions {
  font-family: "Inter", sans-serif;
  margin-top: 0;
  text-align: center;
  max-width: 450px;
  font-weight: 500;
  margin-bottom: 3rem;
  line-height: 1.3;
}


.die-num {
  font-size: 2rem;
}

.roll-dice {
  height: 50px;
  width: 150px;
  border: none;
  border-radius: 6px;
  background-color: var(--main);
  color: var(--second);
  font-size: 1.2rem;
  font-family: "Karla", sans-serif;
  cursor: pointer;
}

.roll-dice:focus {
  outline: none;
}

.roll-dice:active {
  box-shadow: inset 5px 5px 10px -4px rgba(22, 22, 22, 0.5);
}

.statistic {
  position: absolute;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  padding-block: 5px;
  border-bottom: 1px solid #aad4ee;
  color: #aad4ee;
  gap: 8px;
  font-size: 1.2rem;  
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.statistic .ranking {
  background: #aad;
  color: #222;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  text-align: center;
  line-height: 1.2;
  position: relative;
  cursor: pointer;
}
.statistic .ranking .best-rank {
  position: absolute;
  top: 10px;
  left: calc(100% + 5px);
  border-radius: 5px;
  background-color: #0b2434;
  color: white;
  padding: 1em;
  font-size: 0.8rem;
  width: 100px;
  text-align: start;
  line-height: 1.5;
  transform-origin: left;
  transform: scaleX(0);
  transition: 400ms;
}
.statistic .ranking:hover .best-rank {
  transform: scaleX(1);
}
